* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.country-flag {
  width: 20px;
  height: 20px;
  margin-right: 4px;
}

.country-info__boldtext {
  font-weight: 700;
}

.country-item {
  display: flex;
  align-items: center;
  margin-bottom: 5px;

}

p {
  margin-top: 0;
  margin-bottom: 0;
}

.country-info__name {
  font-size: 35px;
}